/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/

body,
button,
input,
select,
textarea {
  color: black;
  font-family: $font__main;
  @include font-size(1);
  line-height: $font__line-height-body;
}

/* Headings */

// h2 {
